:root {
  --rhddx-code--highlight--XML-Color: #aef7b4;
  --rhddx-code--highlight--XML-Color-alt: #ddfdd5f2;
  --rhddx-code--highlight--TOML-Color: #f7b0ae;
  --rhddx-code--highlight--TOML-Color-alt: #fdd6d5f2;
}

/*
** Add the external link icon to any URLs that contain 'patternfly.org', signifying that the user is leaving for the PatternFly site.
*/
a[href*="patternfly.org"]:after {
  content: '';
  display: inline-block;
  background-image: url(../../assets/img/PF_logo.png);
  background-size: contain;
  background-repeat: no-repeat;
  height: 16px;
  width: 17px;
  margin-left: 4px;
  .pf-c-nav__list > .pf-c-nav__item & {
    content: '';
    display: inline-block;
    background-image: url(../../assets/img/PF_logo.png);
    background-size: contain;
    background-repeat: no-repeat;
    height: 16px;
    width: 17px;
    margin-left: 4px;
  }
}

/*
** Add the GitHub icon to any URLs that contain 'github.com', signifying that the user is leaving the site and proceeding to GitHub.
*/
a[href*="github.com"]:after {
  font-family: "Font Awesome 5 Brands";
  font-weight: 400;
  content: "\f09b";
  margin-left: 4px;
  .pf-c-nav__list > .pf-c-nav__item & {
    font-family: "Font Awesome 5 Brands";
    font-weight: 400;
    content: "\f09b";
    margin-left: 4px;
    position: initial;
    background-color: transparent;
  }
}

/*
** Add the CodePen icon to any URLs that contain 'codepen.io', signifying that the user is leaving the site and proceeding to CodePen.
*/
a[href*="codepen.io"]:after {
  font-family: "Font Awesome 5 Brands";
  font-weight: 400;
  content: "\f1cb";
  margin-left: 4px;
  .pf-c-nav__list > .pf-c-nav__item & {
    font-family: "Font Awesome 5 Brands";
    font-weight: 400;
    content: "\f1cb";
    margin-left: 4px;
    position: initial;
    background-color: transparent;
  }
}

/*
** Customize text selection/highlight colors
*/
::-moz-selection { /* Code for Firefox */
  color: #fff;
  background-color: rgb(0, 116, 232);
}

::selection {
  color: #fff;
  background-color: rgb(0, 116, 232);
}

/*
** Position 'Copy' toolbar button
*/
div.code-toolbar > .toolbar {
  top: .3em;
  right: 4.5em;
}

pre[rel] {
  padding-top: 20px;
}
pre {
  clear: both;
  color: #fff;
  background: #03192b;
  padding: 1.5rem !important;
  margin: 1rem !important;
  -moz-tab-size: 2;
  -o-tab-size: 2;
  tab-size: 2;
  -ms-word-break: normal;
  word-break: normal;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  line-height: 20px;
  border-radius: 5px;
  min-width: 100px;
  overflow: hidden;
}
pre[rel]::before {
  font-size: 14px;
  font-weight: 700;
  content: attr(rel);
  color: #fff;
  position: absolute;
  top: .33rem;
  left: .52rem;
  width: 100%;
  padding: 0;
}

pre[rel="CSS"]::before,
pre[rel="SCSS"]::before,
pre[rel="css"]::before,
pre[rel="scss"]::before {
  color: #ff8a00;
}

/*
** Label blocks of code with their language and apply colors
*/
code.highlighter-rouge {
  background: #3e464d;
  color: #fff;
  font-size: 1rem;
  border-radius: 4px;
}
.highlighter-rouge .highlight pre.highlight::before {
  font-size: 14px;
  font-weight: 700;
  position: relative;
  top: -1.3rem;
  left: 88%;
  width: 100%;
  padding: 0;
}
.language-json.highlighter-rouge .highlight pre.highlight::before {
  content: "JSON";
  color: #f7e3a9;
}
.language-toml.highlighter-rouge .highlight pre.highlight::before {
  content: "TOML";
  color: var(--rhddx-code--highlight--TOML-Color-alt);
}
.language-xml.highlighter-rouge .highlight pre.highlight::before {
  content: "XML";
  color: var(--rhddx-code--highlight--XML-Color-alt);
}
.language-css.highlighter-rouge .highlight pre.highlight::before,
.language-scss.highlighter-rouge .highlight pre.highlight::before {
  content: "CSS";
  color: #a0ceff;
}
.language-html.highlighter-rouge .highlight pre.highlight::before {
  content: "HTML";
  color: #a0ceff;
}

code {
  background-color: transparent;
}

/*
** Add the link URL after the link when the page is printed
*/
@media print {
  a[href]:after {
    content: " (" attr(href) ") ";
  }
}
